import React, { Component } from 'react';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import './App.css';
import Toolbar from './components/Toolbar/Toolbar';
import SideDrawer from './components/SideDrawer/SideDrawer';
import Backdrop from './components/Backdrop/Backdrop';
import HomePage from './components/Content/HomePage/HomePage';
import Locations from './components/Content/Locations/Locations';
import Schedule from './components/Content/Schedule/Schedule';
import Technique from './components/Content/Technique/Technique';

class App extends Component {
  state = {
    SideDrawerOpen: false,
    pageContent: ''
  };
  
  drawerToggleClickHandler = () =>{
    this.setState((prevState)=>{
      return {SideDrawerOpen: !prevState.SideDrawerOpen};
    })
  };

  locationClickHandler = () =>{
    this.setState(()=>{
      return {pageContent: 'locations'};
    })
  }

  backdropClickHandler = () => {
    this.setState({SideDrawerOpen: false});
  };

  render(){
    let backdrop;  

    if(this.state.SideDrawerOpen){
      backdrop = <Backdrop click={this.backdropClickHandler}/>
    }
    return (
      <div style={{height: '100%'}}>
        <Toolbar drawerClickHandler={this.drawerToggleClickHandler} locationClicked = {this.locationClickHandler}/>
        <SideDrawer show={this.state.SideDrawerOpen}/>
        {backdrop}
        <main style={{marginTop:'64px'}}>
          <Router>
            <Switch>
              <Route path="/" exact component={HomePage}/>
              <Route path="/locations" component={Locations}/>
              <Route path="/schedule" component={Schedule}/>
              <Route path="/technique" component={Technique}/>
            </Switch>
          </Router>
        </main>
      </div>
    );
  }
}

export default App;
